.photo-gallery {
    padding: 50px 50px;

    background-color: @background-education;
}

.photo-gallery__title {
    margin: 0 0 50px;
}

.photo-gallery__figcaption {
    text-align: center;

    margin-bottom: 10px;
}

.photo-gallery__container {
    width: 800px;
    height: 800px;

    display: flex;
    flex-flow: row wrap;

    background-color: @background-counter;

    margin: 0 auto;
}

.photo-gallery__picture,
.photo-gallery__image {
    width: 400px;
    height: 400px;
}
